
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

body{
  font-family: "Poppins", sans-serif;
}
.container{
  max-width: 1280px;
  margin: 0 auto;
}
nav{
  display: flex;
  justify-content: space-between;

  height: 72px;
align-items: center;
}
*{
  margin: 0;
}
nav ul{
  display: flex;
  list-style: none;
  gap: 24px;

}
button{
  all: unset;
  padding: 6px 16px;
  background-color: red;
  height: 32px;
  color: white;
  font-weight: 500;

}

.hero{
  
  align-items: center;
  height: calc(100vh-72px);


}
.hero h1{
  font-weight: 880;
  font-size: 80px;
  line-height: 102px;
}
.hero p{
  font-weight: 600;
  color: gray;
}

.hero-btn{
  display: flex;
  gap: 35px;
}
.second-btn{
  background: transparent;
  color: green;
  border: 1px solid green;
}
.shopping p {
  margin-bottom: 8px;
  color: black;
}
.brand-icons{
  display: flex;
  gap: 16px;
}